.react-local-toast-icon{pointer-events:none;margin-right:12px;font-size:24px;min-width:24px;min-height:24px}.react-local-toast-info{border:2px solid #3498db}.react-local-toast-info .react-local-toast-icon{color:#3498db}.react-local-toast-loading{border:2px solid #3498db}.react-local-toast-loading .react-local-toast-icon{color:#3498db}.react-local-toast-success{border:2px solid #2ecc71}.react-local-toast-success .react-local-toast-icon{color:#2ecc71}.react-local-toast-warning{border:2px solid #fa983a}.react-local-toast-warning .react-local-toast-icon{color:#fa983a}.react-local-toast-error{border:2px solid #eb2f06}.react-local-toast-error .react-local-toast-icon{color:#eb2f06}.react-local-toast-loading .react-local-toast-icon{animation:rltSpin linear infinite 1.5s}.react-local-toast-text{line-height:1.5;flex-grow:1}.react-local-toast{padding:6px 12px;z-index:9999;background-color:#fff;color:#333;font-size:14px;box-shadow:0 3px 6px -4px rgba(0,0,0,.1215686275),0 6px 16px rgba(0,0,0,.0784313725),0 9px 28px 8px rgba(0,0,0,.0509803922);border-radius:3px;min-width:150px;max-width:min(350px,80vw - 8px);min-height:30px;display:flex;justify-content:center;align-items:center}@keyframes rltTop{from{transform:translateY(25%) scale(1);opacity:0}to{transform:translate(0, 0) scale(1);opacity:1}}@keyframes rltBottom{from{transform:translateY(-25%) scale(1);opacity:0}to{transform:translate(0, 0) scale(1);opacity:1}}@keyframes rltLeft{from{transform:translateX(25%) scale(1);opacity:0}to{transform:translate(0, 0) scale(1);opacity:1}}@keyframes rltRight{from{transform:translateX(-25%) scale(1);opacity:0}to{transform:translate(0, 0) scale(1);opacity:1}}@keyframes rltSpin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}